<template>
  <div class="footer">
    <i-tab-bar :current="currentTabbar" color="#4878FF" @change="handleChange">
      <i-tab-bar-item key="homepage" icon="homepage_fill" current-icon="homepage_fill" title="首页"></i-tab-bar-item>
      <i-tab-bar-item key="company" icon="group_fill" current-icon="group_fill" title="公司"></i-tab-bar-item>
      <i-tab-bar-item key="listing" icon="remind_fill" current-icon="remind_fill" count="3" title="上市"></i-tab-bar-item>
      <i-tab-bar-item key="analysis" icon="dynamic_fill" current-icon="dynamic_fill" dot title="分析"></i-tab-bar-item>
      <i-tab-bar-item key="news" icon="interactive_fill" current-icon="interactive_fill" dot title="资讯"></i-tab-bar-item>
    </i-tab-bar>
  </div>
</template>
<script>
export default {
  name: 'test_tabbar',
  data () {
    return {
      currentTabbar: 'homepage'
    }
  },
  methods: {
    handleChange (event) {
      console.log(event)
      this.currentTabbar = event.mp.detail.key
    }
  }
}
</script>

<style scoped>

</style>



